*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
body{
    background-color: black;
    color: white;
}
.today{
    display: flex;
    justify-content: center;
    padding-bottom: 20px;
    padding-top: 20px;
    span{
        color: yellow;
        font-size: 36px;
        padding-left: 20px;
    }
}
.img{
    display: flex;
    flex-direction: column;
    align-items: center;
    img{
        padding-top: 20px;
    }
}